.chatbot-outer-frame{
    background-color: #cecece;
    padding: 8px 12px;
    margin: 0 auto 0 0;
    margin-left: 9px;
    margin-top: 9px;
    max-width: 80%;
    width: auto;
    display: table;
    box-sizing: border-box;
}

.chatbot-inner-frame {
    background-color: rgb(230, 225, 216);
    margin: 0 auto 0 0;
    padding: 8px 12px;
}


.chatbot-bot-reply{
    background-color: rgb(248, 216, 156);
    display: table;
    max-width: 80%;
    width: auto;
}