body {
    background-color: rgb(187, 174, 207);
}


.chatbot-box {  
    position: absolute;
    width: 320px;
    height: 470px;
    bottom: 0;
    right: 0;
    margin: 10px;
    border-radius: 20px;
    background-color: #cecece;
    box-shadow: 0 0 12px rgb(0 0 0 / 60%);
    background-size: cover;
}  


.chatbot-box-header {
    display: flex;
    justify-content: space-around;
    background-color: rgb(24, 74, 131);
    height: 60px;
    width: 100%;
    right: 0;
    border-radius: 10px 10px 0px 0px;
}

.chatbot-box-logo{
    display: flex;
    padding: 5px 3px 5px 5px;
}

.chatbot-box-header-wrapper {
    position: absolute;
    width: 100%;
    padding-top: 7px;
    box-shadow: 0 0 12px rgba(255, 255, 255, 0.6);
    background: rgb(248, 41, 41);
    border-radius: 10px 10px 0px 0px;
}

.chatbot-box-description {
    display: inline-block;
    padding: 1px 1px 1px 1px;
    padding-left: 15px;
    width: 100%;
    padding-top: 5px;
    
}

.chatbot--bot--name {
    font-weight: bold;
    color: #ffffff;
    font-size: 12px;
    padding-left: 1px;
}

.chatbot--bot--descp{
    display: flex;
    color: #ffffff;
    font-size: 9px;
}

.chatbot-box-options {
    display: flex;
    padding: 1px 1px 1px 1px;
}

.chatbot-chat-icon {
    position: absolute;
    bottom: 0;
    right: 0;
    background-color: rgb(127, 173, 241); 
}


.message-outer-input-box {
    display: flex;
    background: rgb(255, 255, 255);
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 5;
    
}


.message-input-box {
    display: flex;
    padding: 8px 64px 8px 16px;
    margin: 0 auto;
}

.message-attachment{
    position: absolute;
    padding-top: 7px;
}

.message-send-button {
    position: absolute;
}
 
.message-placeholder-input {
    width: 100%;
    padding: 8px 50px 8px 16px;
    padding-left: 30px;
    font: inherit;
    box-sizing: border-box;
    border: 0;
    border-bottom: 1px solid #fff;
    color: #000000;
    background: transparent;
    outline: 0;
} 

.test-messages-box {

    height: 50px;
    bottom: 320px;
    position: absolute;
    width: 85%;
    border: 1px solid;
    background-color: #000000;
    border-radius: 6px; 
    padding: 15px;
    margin: 5px;
}

.bot--messages--box {
    border: 1px solid;
    background-color: #f1f1f1;
    border-radius: 6px; 
    padding: 1px;
    margin: 5px 0;
}

.bot--messages1--box2 {
    background-color: #f1f1f1;
    font-size: 12px; 
    padding-top: 6px;
    padding-left: 10px;
}
